/* Fix AEM oddities and things that shouldn't be there */

.xf-content-height {
  margin: unset !important;
  min-height: 0 !important;
}

.xf-web-container div.container {
  margin-left: revert;
  margin-right: revert;
  padding: revert;
}

div.image {
  margin-bottom: revert;
  overflow: revert;
}
